.greetingForm {
    max-width: 500px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    background-color: #f8f9fa;
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
    align-items: flex-start;
    margin-bottom: 10px;
}

.error {
    color: #dc3545;
    font-size: 0.875rem;
    min-height: 20px; /* Фиксированная высота */
    visibility: hidden; /* Скрываем когда нет ошибки */
    margin-top: 4px;
}

.error:not(:empty) {
    visibility: visible; /* Показываем при наличии текста */
}

.input {
    flex-grow: 1;
    padding: 8px 12px;
    border: 1px solid #ced4da;
    border-radius: 4px;
    font-size: 1rem;
    transition: border-color 0.3s ease;
}

.input:focus {
    outline: none;
    border-color: #80bdff;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.errorInput {
    border-color: #dc3545 !important;
    box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.button {
    padding: 8px 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}

.button:active {
    background-color: #0056b3;
}

.button:disabled {
    background-color: #6c757d;
    cursor: not-allowed;
    opacity: 0.65;
}

.text {
    font-weight: 500;
    margin-bottom: 8px;
    color: #343a40;
}

.greeting {
    margin-top: 10px;
    color: #28a745;
    font-weight: 500;
}